@keyframes bounce-7 {
  0%   { transform: scale(1,1)      translateY(-50px); }
  10%  { transform: scale(1.1,.9)   translateY(0); }
  30%  { transform: scale(.9,1.1)   translateY(-25px); }
  50%  { transform: scale(1.05,.95) translateY(0); }
  57%  { transform: scale(1,1)      translateY(-8px); }
  64%  { transform: scale(1,1)      translateY(0); }
  81% { transform: scale(1,1)      translateY(-4px); }
  90% { transform: scale(1,1)      translateY(0px); }
  100% { transform: scale(1,1)      translateY(0px); }
}

@keyframes expand {
  0% {
    height: 0;
  }
  100% {
    height: 40px
  }
}

@keyframes collapse {
  0% {
    height: 40px;
  }
  100% {
    height: 0;
  }
}

@keyframes fadeInWithSlideRight {
  0% {
    opacity: 0;
    transform: translateX(-40%);
  }
  100% {
    opacity: 1;
    transform: translateX(0%);
  }
}

@keyframes fadeInWithSlideLeft {
  0% {
    opacity: 0;
    transform: translateX(40%);
  }
  100% {
    opacity: 1;
    transform: translateX(0%);
  }
}
.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}
.noLink {
