/* -------------------- NAVBAR STYLINGS --------------------- */


.navbar {
  background-color: rgb(0, 143, 149);
  font-family: Khula-Bold;
}
.navbar-collapse {
  justify-content: flex-end;
  margin-right: 2em;
}
.collapsing {
  position: relative;
  overflow: hidden;
  animation: collapse;
  animation-fill-mode: forwards;
  animation-duration: 300ms;
  animation-timing-function: ease;
}

.expanding {
  position: relative;
  overflow: hidden;
  animation: expand;
  animation-fill-mode: forwards;
  animation-duration: 300ms;
  animation-timing-function: ease;
}

.navbar-brand {
  font-size: 28px;
  padding-left: 2em;
}
.period {
  transform: translateY(-50px);
}
.period {
  animation: bounce-7;
  animation-delay: 1s;
  animation-duration: 1s;
  animation-timing-function: ease-out;
  animation-fill-mode: forwards;
}

.titleSpecial,
.otherTitleSpecial {
  opacity: 0%;
}

.otherTitleSpecial {
  display: inline-block;
  animation: fadeInWithSlideLeft;
  animation-duration: 600ms;
  animation-fill-mode: forwards;
  font-size: 34px;
}

.titleSpecial {
  display: inline-block;
  animation: fadeInWithSlideRight;

  animation-duration: 600ms;
  animation-fill-mode: forwards;
}

.navbar-collapse {
  text-align: center;
}
.nav-link {
  color: white !important;
  font-family: Khula-Regular !important;
}

.nav-link:hover {
  color: rgb(248, 128, 146) !important;
}
.nav-link:active {
  border-bottom: 2px solid black;
}

.navbar {
  justify-content: space-between;
}

@media(max-width: 991px) {
  .navbar {
    justify-content: center;
  }
  .navbar-brand {
    padding-right: 0;
    padding-left: 0;
  }

  .navbar-toggler {
    position: absolute;
    right: 10px;
    top: 14px;
  }
  .navbar-collapse {
    margin-right: 0;
  }
  .nav-item {
    visibility: hidden;
    flex: 0;
  }
  .navbar-nav {
    flex-direction: row;
  }
  
  .show .nav-item {
    visibility: visible;
  }
  .show .nav-item {
    flex: 1;
    transition: 300ms;
    transition-timing-function: ease-out;
  }
}


