/*---------------------------------------BLOG PAGE STYLINGS -----------------------------------------------------------------------------*/
.blogRow {
  justify-content: center;
  text-align: center;
}
.blogEntry {
  border-radius: 15px;
  margin-top: 20px;
  margin-bottom: 20px;
}
.blogTitle {
  font-family: Khula-Bold;
  position: relative;
  align-items: center;
  flex-wrap:wrap;
  color: white;
  text-align: center;
  font-size: 30px;
  border-radius: 15px;
  padding: 5px;
  margin: 5px;
  transition: 300ms;
}

@media(min-width: 993px) {
  .blogTitleText {
    text-align: start;
    padding-left: 20px;
  }
}

@media(max-width: 767px) {
  .blogTitle {
    justify-content: center;
  }
}

.blogArrow {
  height: fit-content;
  position: absolute;
  color: white;
  font-size: 20px;
  transition: 300ms;
}

@media(min-width: 768px) {
  .blogArrow {
    right: 25px;
    top: -40px;
    color: black;
  }
}

@media(min-width: 992px) {
  .blogArrow {
    right: 25px;
    top: 15px;
    color:white;
  }
}

@media(max-width: 767px) {
  .blogArrow {
    color: white;
    bottom: 0;
    right: 15px;
  }
}
.blogArrowIcon {
  transition: 300ms;
}
.rotate {
  transform:rotate(-180deg);
  transition: 300ms;
}
.blogContent {
  text-align: left;
  font-size: 20px;
  margin: 20px;
  margin-top: 40px;
  position: relative;
}
.blogContent.hidden {
  overflow: hidden;
  height: 0;
  transition: 300ms;
}
@media (hover: hover) and (pointer: fine) {
  .blogEntry:hover .blogContent.hidden {
    position: relative;
    height: 40px;
  }
}

.dateBox {
  text-align: center;
  padding: 7px;
